body {
  //margin: 0;
  //padding: 0;
  background-color: $bg_second_color;
  background-image: url("../img/promo-img.jpg");
  background-size: 103% 825px;
  background-position: center top;
  background-repeat: no-repeat;
  object-fit: cover;

  @media (max-width: $tablet_max) {
    background-size: 100% 439px;
    background-position: center -49px
    //object-fit: none;
  }

  @media (max-width: $mobile_max) {
    min-width: $mobile_min;
    background-image: url("../img/promo-img-mobile.jpg");
    background-size: 174% 396px;
    background-position: center -2px;
  }
  //overflow: hidden;
}
